import React, { useContext } from "react";
import { SearchList } from "./HomeElements";
import LocalContext from "./LocalContext";
import { H2, QueryItem } from "../components/HomeElements";
import { BsXSquare } from "react-icons/bs";

function SearchResults() {
  const {
    queries,
    setQueries,
    apiCall,
    queryListEmpty,
    setQueryListEmpty,
  } = useContext(LocalContext);

  const getQueryImage = (newVal) => {
    apiCall(newVal);
  };

  const removeItem = (id) => {
    if (queries.length === 1) {
      setQueryListEmpty(true);
    }
    const items = queries.filter((query) => query.key !== id);
    setQueries(items);
  };

  return (
    <SearchList>
      <H2>Query List:</H2>
      {queryListEmpty && <p>Empty. Please add more.</p>}

      <ul>
        {queries.map((query) => {
          return (
            <div
              key={query.key}
              style={{
                display: "flex",
              }}
            >
              <li
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  margin: "2px",
                }}
              >
                <BsXSquare
                  style={{
                    color: "#000",
                    backgroundColor: "red",
                    marginRight: "5px",
                  }}
                  onClick={() => removeItem(query.key)}
                />

                <QueryItem onClick={() => getQueryImage(query.value)}>
                  {query.value}
                </QueryItem>
              </li>
            </div>
          );
        })}
      </ul>
    </SearchList>
  );
}

export default SearchResults;
